import React from "react";
import axios from "axios";
export default class GetFilmInfo extends React.Component {
  _isMounted = false;
  state = {
    info: [],
  };

  componentDidMount() {
    this._isMounted = true;
    this.search();
  }

  componentWillUpdate(nextProps, nextState) {
    this.search();
  }
  async search() {
    axios.get(this.props.URL).then((res) => {
      const data = res.data;
      const image = data.Poster;
      // console.log(image);
      console.log(data);
      if (this._isMounted) {
        this.setState({
          info: [
            <>
              <img key="0" src={image} alt="poster" width="250" height="350" />
              <p key="1">
                {data.Title} ({data.Year})
              </p>
            </>,
            <p key="2">Rated: {data.Rated}</p>,
            <p key="3">Starring: {data.Actors}</p>,
            <p key="4">Directed by: {data.Director}</p>,
            <p key="5">
              <em>{data.Plot}</em>
            </p>,
          ],
        });
      }
    });
  }
  componentWillUnmount() {
    this._isMounted = false;
  }
  render() {
    return (
      <div className="Info">
        <ul>{this.state.info}</ul>
      </div>
    );
  }
}
